import {
    Component, Input, OnInit, AfterViewInit, ChangeDetectorRef, Optional, OnDestroy, Injector
} from '@angular/core';
import * as _ from 'lodash-es';
import { Subscription } from 'rxjs';
import { noDataImg } from '../../assets/resource';
import { ApiService } from '../../utils/api.service';
import { CommonUtil } from '../../utils/common.tool';
import { FavoritesTool } from '../../utils/favorites.tool';
import { ChangeType, Organization, OrgRequestParam } from '../../utils/index.type';
import { StateMgrTool } from '../../utils/state.mgr.tool';

@Component({
    selector: 'lib-favorites',
    templateUrl: './favorites.component.html',
    styleUrls: ['./favorites.component.css'],
})
export class FavoritesComponent implements OnInit, AfterViewInit, OnDestroy {

    @Input() primaryField: string;
    @Input() multiSelect: boolean;

    allChecked = false;
    selections: any[] = [];
    allIndexId = 'allIndexId';
    noDataImg = noDataImg;
    items = [];
    favoritesList = [];

    private selectionsSub: Subscription;
    private favoritesSub: Subscription;

    constructor(
        private apiService: ApiService,
        private favoritesTool: FavoritesTool,
        private stateMgr: StateMgrTool,
        private cdr: ChangeDetectorRef,
        private injector: Injector
    ) {
        this.allIndexId = CommonUtil.newGuid();
    }

    ngOnDestroy(): void {
        this.selectionsSub.unsubscribe();
        this.favoritesSub.unsubscribe();
    }

    ngOnInit(): void {
        this.favoritesSub = this.favoritesTool.favoritesSubject.subscribe(data => {
            this.favoritesList = data;
            this.loadFavoritesList();
        });
        this.selectionsSub = this.stateMgr.selectionsSubject.subscribe(data => {
            if (data.changeType === ChangeType.ValueChanged) {
                this.selections = _.cloneDeep(data.value);
            } else if (data.changeType === ChangeType.Remove) {
                this.unSelectLine(data.value);
            } else if (data.changeType === ChangeType.Append) {
                this.selectLine(data.value);
            }
            this.toggleChecked();
            this.cdr.detectChanges();
        });
    }

    ngAfterViewInit(): void {

    }

    assembleData() {
        this.favoritesSub = this.favoritesTool.favoritesSubject.subscribe(data => {
            this.favoritesList = data;
            this.loadFavoritesList();
        });
    }

    deleteFromFavorites(e: any, item: any) {
        this.favoritesTool.toggleFavorites(e, item);
    }

    toggleAll(e: any) {
        if (!this.allChecked) {
            this.checkAll();
        } else {
            this.unCheckAll();
        }
        e.preventDefault();
    }

    toggleOne(data, e) {
        if (data.checked) {
            if (this.multiSelect) {
                this.emitSelections(data, ChangeType.Remove);
            } else {
                this.emitSelections([]);
            }
        } else {
            if (this.multiSelect) {
                this.emitSelections(data, ChangeType.Append);
            } else {
                this.emitSelections(new Array(data));
            }
        }
        e.preventDefault();
    }

    /**
     * 用于数据展示
     */
    loadFavoritesList() {
        this.items = [];
        this.favoritesList.forEach(res => {
            const indexId = CommonUtil.newGuid();
            const item: Organization = {
                orgId: res.orgId,
                code: res.code,
                name: res.name,
                orgAllPath: res.orgAllPath,
                path: res.path,
                layer: Number(res.layer),
                indexId
            };
            if (CommonUtil.findIndex(res[this.primaryField], this.primaryField, this.selections) !== -1) {
                item.checked = true;
            }
            this.items.push(item);
        });
        this.items = this.items.slice();
        this.cdr.detectChanges();
    }

    /**
     * 全选后处理
     * @param dataArr 全选的值
     */
    checkAll() {
        this.items.forEach(item => {
            if (CommonUtil.findIndex(item[this.primaryField], this.primaryField, this.selections) === -1) {
                this.emitSelections(item, ChangeType.Append);
            }
        });
    }

    /**
     * 全部取消
     * @param dataArr 取消的数据
     * @returns void
     */
    unCheckAll() {
        const checkeds = this.items.filter(item => item.checked);
        checkeds.forEach(item => {
            this.emitSelections(item, ChangeType.Remove);
        })
    }

    /**
     * 当前行选中
     * @param data 当前行的数据（）
     */
    private selectLine(data: any) {
        if (this.multiSelect) {
            this._insertOneIntoSelections(data);
        } else {
            this.selections = [];
            this.items.forEach(item => {
                if (item.checked) {
                    item.checked = false;
                }
            });
            this._insertOneIntoSelections(data);
        }
        if (this.isAllChecked()) {
            this.allChecked = true;
        }
    }

    /**
     * 单选取消选中
     * @param data 取消选中行
     */
     private unSelectLine(data: any) {
        const index = CommonUtil.findIndex(data[this.primaryField], this.primaryField, this.selections);
        if (index > -1) {
            this.selections.splice(index, 1);
        }
        this.setChecked(data, false);
        if (this.allChecked) {
            this.allChecked = false;
        }
    }

    private setChecked(data: any, checked: boolean) {
        const itemIndex = CommonUtil.findIndex(data[this.primaryField], this.primaryField, this.items);
        if (itemIndex > -1) {
            this.items[itemIndex].checked = checked;
        }
    }

    private emitSelections(selections: any, changeType = ChangeType.ValueChanged, from = 'favorite') {
        this.stateMgr.emitSelections(selections, changeType, from);
    }

    toggleFavorites(e: any, item: any) {
        this.favoritesTool.toggleFavorites(e, item);
    }

    /**
     * 全部取消勾选
     */
    clearCheckeds() {
        this.items.forEach(item => {
            item.checked = false;
        });
        this.allChecked = false;
        this.selections = [];
    }

    /**
     * 选中并插入当前行数据到右侧
     * @param data 当前行的数据
     * @param collection 暂存当前数据的集合
     */
    _insertOneIntoSelections(data: any) {
        // 如果当前数据已经移入右侧数据，则返回。
        if (CommonUtil.findIndex(data[this.primaryField], this.primaryField, this.selections) > -1) {
            return;
        }
        this.setChecked(data, true);
        this.selections.push(data);
        this.selections = this.selections.slice();
    }

    /**
     * 翻页检查：人员展示区的勾选状态与已选人员区是否对应
     */
    toggleChecked() {
        this.items.forEach(item => {
            if (CommonUtil.findIndex(item[this.primaryField], this.primaryField, this.selections) > -1) {
                item.checked = true;
            } else {
                item.checked = false;
            }
        });
        if (this.isAllChecked()) {
            this.allChecked = true;
        } else {
            this.allChecked = false;
        }
    }

    private isAllChecked(): boolean {
        let count = 0;
        let allChecked = false;

        this.items.forEach(item => {
            this.selections.forEach(selection => {
                if (item[this.primaryField] === selection[this.primaryField]) {
                    count += 1;
                }
            });
        });
        if (this.items.length === count && count > 0) {
            allChecked = true;
        }
        return allChecked;
    }

    trackByFn(index, item) {
        return index;
    }

}
