import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { ChangeType, OrgRequestParam, Param } from './index.type';
import { Observable, Subject } from 'rxjs';

@Injectable()
export class StateMgrTool {

  private slSubject = new Subject<Param>();

  constructor(
    private http: HttpClient
  ) { }

  get selectionsSubject(): Observable<Param> {
    return this.slSubject.asObservable();
  }

  emitSelections(value: any, changeType: ChangeType, from: string = 'other') {
    this.slSubject.next({
      value,
      changeType,
      from
    });
  }

}
