import {
    Component, Input, EventEmitter, Output, OnInit, AfterViewInit, ChangeDetectorRef, Optional, OnDestroy, Injector
} from '@angular/core';
import * as _ from 'lodash-es';
import { Subscription } from 'rxjs';
import { noDataImg } from '../../assets/resource';
import { ApiService } from '../../utils/api.service';
import { CommonUtil } from '../../utils/common.tool';
import { FavoritesTool } from '../../utils/favorites.tool';
import { ChangeType, OrgRequestParam } from '../../utils/index.type';
import { StateMgrTool } from '../../utils/state.mgr.tool';

@Component({
    selector: 'lib-search',
    templateUrl: './srp.component.html',
    styleUrls: ['./srp.component.css'],
})
export class SearchComponent implements OnInit, AfterViewInit, OnDestroy {

    @Input() primaryField: string;
    @Input() multiSelect: boolean;
    @Input() enableFavorites: boolean;
    @Input() searchText: string;
    @Input() absOrgType: string;
    @Input() notGetStopOrg: boolean;
    @Input() pageSize: number;
    @Input() set onSearch(value: any) {
        if (!!value) {
            this.loadSearchResult();
        } else {
            this.itemTotalCount = 0;
            this.totalItems = [];
        }
    }
    @Output() closePanelChange = new EventEmitter<boolean>();

    allChecked = false;
    columns: any;
    selections: any[] = [];

    cloneSelections = [];
    allIndexId = 'allIndexId';
    noDataImg = noDataImg;
    totalItems = [];
    perPageItems = [];
    favoritesList = [];
    pageIndex = 1;
    pageTotal: number = 0;
    itemTotalCount = 0;

    private selectionsSub: Subscription;
    private favoritesSub: Subscription;

    constructor(
        private apiService: ApiService,
        private favoritesTool: FavoritesTool,
        private stateMgr: StateMgrTool,
        private cdr: ChangeDetectorRef,
        private injector: Injector
    ) {
    }

    ngOnDestroy(): void {
        this.selectionsSub.unsubscribe();
        if (this.enableFavorites) {
            this.favoritesSub.unsubscribe();
        }
    }

    ngOnInit(): void {
        if (this.enableFavorites) {
            this.favoritesSub = this.favoritesTool.favoritesSubject.subscribe(data => {
                this.favoritesList = data;
                this.setFavorites();
            });
        }
        this.selectionsSub = this.stateMgr.selectionsSubject.subscribe(data => {
            if (data.changeType === ChangeType.ValueChanged) {
                this.selections = _.cloneDeep(data.value);
            } else if (data.changeType === ChangeType.Remove) {
                this.unSelectLine(data.value);
            } else if (data.changeType === ChangeType.Append) {
                this.selectLine(data.value);
            }
            this.toggleChecked();
            this.cdr.detectChanges();
        });
    }

    ngAfterViewInit(): void {

    }

    private setFavorites() {
        this.perPageItems.forEach(item => {
            if (CommonUtil.findIndex(item[this.primaryField], this.primaryField, this.favoritesList) !== -1) {
                item.inFavorites = true;
            } else {
                item.inFavorites = false;
            }
        })
    }

    turnOverPage(dir: string, num: number) {
        if ((dir === 'pre' && this.pageIndex === 1) || (dir === 'next' && this.pageIndex === this.pageTotal)) {
            return;
        }
        this.pageIndex = this.pageIndex + num;
        this.switchPage({ pageIndex: this.pageIndex });
    }

    beforePageIndexChange(inputEl: KeyboardEvent, maxNumber) {
        const target = inputEl.target as any;
        const currentPageNumber = Number(target.value);
        if (currentPageNumber > maxNumber) {
            target.value = maxNumber;
        } else {
            if (currentPageNumber < 1) {
                target.value = 1;
            }
        }
        this.pageIndex = Number(target.value);
    }

    closePanel() {
        this.totalItems = [];
        this.perPageItems = [];
        this.itemTotalCount = 0;
        this.pageTotal = 0;
        this.closePanelChange.emit(true);
    }

    toggleAll() {
        if (!this.allChecked) {
            this.checkAll();
        } else {
            this.unCheckAll();
        }
        this.emitSelections(this.selections, ChangeType.ValueChanged);
    }

    toggleOne(data, e) {
        if (data.checked) {
            if (this.multiSelect) {
                this.emitSelections(data, ChangeType.Remove);
            } else {
                this.emitSelections([]);
            }
        } else {
            if (this.multiSelect) {
                this.emitSelections(data, ChangeType.Append);
            } else {
                this.emitSelections(new Array(data));
            }
        }
        e.preventDefault();
    }

    /**
     * 用于数据展示
     */
    loadSearchResult() {
        const param: OrgRequestParam = {
            absOrgType: this.absOrgType,
            notGetStopOrg: this.notGetStopOrg,
            getCurUser: false,
            getOrgAllPath: true,
            codeOrName: this.searchText
        };
        this.apiService.getOrganization(param).subscribe((res: any) => {
            if (!res || !res.data) {
                return;
            }
            this.itemTotalCount = res.data.length;
            this.totalItems = res.data;
            this.pageTotal = Math.ceil(this.itemTotalCount / this.pageSize);
            this.switchPage({ pageIndex: 1 });
            this.toggleChecked();
            this.cdr.detectChanges();
        });
    }

    switchPage(pageInfo: { pageIndex: number }) {
        if (!pageInfo || pageInfo.pageIndex < 1) {
            return;
        }
        let startIndex = (pageInfo.pageIndex - 1) * this.pageSize;
        let endIndex = startIndex + this.pageSize;
        this.perPageItems = this.totalItems.slice(startIndex, endIndex);
        this.perPageItems.forEach(res => {
            const indexId = CommonUtil.newGuid();
            res.checked = false;
            res.inFavorites = false;
            res.indexId = indexId;
            if (CommonUtil.findIndex(res[this.primaryField], this.primaryField, this.selections) !== -1) {
                res.checked = true;
            }
            if (CommonUtil.findIndex(res[this.primaryField], this.primaryField, this.favoritesList) !== -1) {
                res.inFavorites = true;
            }
        });
        this.perPageItems = this.perPageItems.slice();
    }

    /**
     * 全选后处理
     * @param dataArr 全选的值
     */
    checkAll() {
        this.perPageItems.forEach(item => {
            this._insertOneIntoSelections(item);
            item.checked = true;
        });
    }

    /**
     * 全部取消
     * @param dataArr 取消的数据
     * @returns void
     */
    unCheckAll() {
        this.perPageItems.forEach(item => {
            this.selections.forEach((selection, index) => {
                if (item[this.primaryField] === selection[this.primaryField]) {
                    this.selections.splice(index, 1);
                }
            });
            item.checked = false;
        });
    }

    /**
     * 当前行选中
     * @param data 当前行的数据（）
     */
    private selectLine(data: any) {
        if (this.multiSelect) {
            this._insertOneIntoSelections(data);
        } else {
            this.selections = [];
            this.perPageItems.forEach(item => {
                if (item.checked) {
                    item.checked = false;
                }
            });
            this._insertOneIntoSelections(data);
        }
        if (this.isAllChecked()) {
            this.allChecked = true;
        }
    }

    /**
     * 单选取消选中
     * @param data 取消选中行
     */
    private unSelectLine(data: any) {
        const index = CommonUtil.findIndex(data[this.primaryField], this.primaryField, this.selections);
        if (index > -1) {
            this.selections.splice(index, 1);
        }
        this.setChecked(data, false);
        if (this.allChecked) {
            this.allChecked = false;
        }
    }

    private setChecked(data: any, checked: boolean) {
        const itemIndex = CommonUtil.findIndex(data[this.primaryField], this.primaryField, this.perPageItems);
        if (itemIndex > -1) {
            this.perPageItems[itemIndex].checked = checked;
        }
    }

    private emitSelections(selections: any, changeType = ChangeType.ValueChanged, from = 'search') {
        this.stateMgr.emitSelections(selections, changeType, from);
    }

    toggleFavorites(e: any, item: any) {
        this.favoritesTool.toggleFavorites(e, item);
    }

    /**
     * 选中并插入当前行数据到右侧
     * @param data 当前行的数据
     * @param collection 暂存当前数据的集合
     */
    _insertOneIntoSelections(data: any) {
        // 如果当前数据已经移入右侧数据，则返回。
        if (CommonUtil.findIndex(data[this.primaryField], this.primaryField, this.selections) > -1) {
            return;
        }
        this.setChecked(data, true);
        this.selections.push(data);
        this.selections = this.selections.slice();
    }

    /**
     * 翻页检查：人员展示区的勾选状态与已选人员区是否对应
     */
    toggleChecked() {
        this.perPageItems.forEach(item => {
            if (CommonUtil.findIndex(item[this.primaryField], this.primaryField, this.selections) > -1) {
                item.checked = true;
            } else {
                item.checked = false;
            }
        });
        if (this.isAllChecked()) {
            this.allChecked = true;
        } else {
            this.allChecked = false;
        }
    }

    private isAllChecked(): boolean {
        let count = 0;
        let allChecked = false;

        this.perPageItems.forEach(item => {
            this.selections.forEach(selection => {
                if (item[this.primaryField] === selection[this.primaryField]) {
                    count += 1;
                }
            });
        });
        if (this.perPageItems.length === count && count > 0) {
            allChecked = true;
        }
        return allChecked;
    }

    trackByFn(index, item) {
        return index;
    }

}
