export class CommonUtil {
    static newGuid() {
        const res = 'xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx'.replace(/[xy]/g, function (c) {
            // tslint:disable-next-line:no-bitwise
            const r = Math.random() * 16 | 0, v = c === 'x' ? r : (r & 0x3 | 0x8);
            return v.toString(16);
        });
        return res;
    }
    /**
     * 判断是否在内
     * @param value value
     * @param field field
     * @param array array
     * @returns 位置索引
     */
    static findIndex(value: string, field: string, array: any[]): number {
        if (!field || !value) {
            throw new Error('前2个参数必填');
        }
        return array.findIndex(item => value === item[field]);
    }
}
