import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { OrgRequestParam, SaveGroupReqeustParam } from './index.type';
import { of } from 'rxjs';

@Injectable()
export class ApiService {

  absUrl: string;
  groupApiUrl: string;
  constructor(
    private http: HttpClient
  ) { }

  /**
    * 获取行政组织或系统用户
    * @returns observable
    */
  getOrganization(param: OrgRequestParam) {
    const options = {
      headers: new HttpHeaders({
        'Content-Type': 'application/json',
      }),
    };
    return this.http.post(this.absUrl + '/org', param, options);
  }

  getOrgGroups(absOrgType: string) {
    const options = {
      headers: new HttpHeaders({
        'Content-Type': 'application/json',
      }),
    };
    return this.http.get(this.groupApiUrl + '/' + absOrgType, options);
  }

  getGroupMembers(groupId: string, absOrgType: string) {
    const options = {
      headers: new HttpHeaders({
        'Content-Type': 'application/json',
      }),
    };
    if (!groupId) {
      return of(null);
    }
    const url = this.groupApiUrl + '/' + groupId + '/absOrgType/' + absOrgType;
    return this.http.get(url, options);
  }

  saveGroup(groupInfo: SaveGroupReqeustParam) {
    const options = {
      headers: new HttpHeaders({
        'Content-Type': 'application/json',
      }),
    };
    return this.http.post(this.groupApiUrl, groupInfo, options);
  }
}
