export interface OrgRequestParam {
    absOrgType: string; // 组织类型，默认为系统组织
    layer?: number; // 获取第几层的组织
    codeOrName?: string; // 根据编号和名称模糊搜索
    name?: string; // 根据名称模糊搜索
    parentID?: string; // 根据父节点id获取下级组织
    isSearchTree?: boolean; // 搜索时，是否返回树结构，默认false
    getCurUser?: boolean;   // 是否获取当前用户，默认true
    notGetStopOrg?: boolean; // 不获取停用组织，默认true
    id?: string; // 组织id页,
    idList?: string[]; // 根据组织id获取组织完整信息
    getAllParentOrg?: boolean; // 是否获取全部上级组织(包含兄弟组织)，默认true
    getOrgAllPath?: boolean;   //是否获取组织全路径，用下滑线分割
    getOrgAllPathInfo?: boolean;  //根据组织id全部上级组织(不包含兄弟组织)
    withAllSub?: boolean; // 根据组织id获取全部子集，默认false
    layerList?: number[]; // 分层加载情况下，一次性加载多少层级的数据
}

export enum ChangeType {
    Append = 'Append',
    Remove = 'Remove',
    ValueChanged = 'ValueChanged'
}

export interface Param {
    value: any;
    changeType: ChangeType,
    from: string
}

export interface Organization {
    orgId: string;
    code: string;
    name: string;
    orgAllPath: string;
    path: string;
    layer: number;
    checked?: boolean;
    indexId?: string;
}

export interface Group {
    public: Array<GroupItem>;
    personal: Array<GroupItem>;
}

export interface GroupItem {
    id: string;
    title: string;
    index?: string;
    description?: string;
    total: number;
    avatar?: string;
    checked?: boolean;
}


export interface SaveGroupReqeustParam {
    groupId?: string;
    groupCode?: string;
    title?: string;
    description?: string;
    absOrgType?: string;
    addIds?: Array<string>;
    deleteIds?: Array<string>;
    changeType: Number;
}