import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable, of, Subject } from 'rxjs';

@Injectable()
export class FavoritesTool {

    absOrgType = 'System_organization';
    busNum = 'common';
    idField = 'userId';
    favoritesList = [];
    absRfQueryUrl = '/api/runtime/sys/v1.0/absRecentContacts/query';
    absRfModifyUrl = '/api/runtime/sys/v1.0/absRecentContacts/modify';
    private subject = new Subject<any>();

    constructor(private http: HttpClient) {

    }

    /**
     * 操作最近联系人或收藏夹
     * @param param any
     * @returns observable
     */
    curdRecentContactsOrFavorites(actionToken: string, param: any) {
        let url = this.absRfQueryUrl;
        if (actionToken === 'modify') {
            url = this.absRfModifyUrl;
        }
        const options = {
            headers: new HttpHeaders({
                'Content-Type': 'application/json',
            })
        };
        return this.http.post(url, param, options);
    }

    get favoritesSubject(): Observable<any> {
        return this.subject.asObservable();
    }

    setFavoritesList(value: any) {
        this.subject.next(value);
    }

    /**
     * 获取收藏夹中人员列表
     */
    getFavoritesList(): Observable<any> {
        const param: any = {
            absOrgType: this.absOrgType,
            bizId: this.busNum,
            objectValue: 'CollectOrg'
        };
        return this.curdRecentContactsOrFavorites('query', param);
    }

    /**
     * 加入或取消收藏
     * @param e 鼠标事件
     * @param id 标识
     * @returns Observable
     */
    toggleFavorites(e: any, object: any) {
        const id = object[this.idField];
        let action = 'add';
        const param: any = {
            absOrgType: this.absOrgType,
            bizId: this.busNum,
            objectValue: 'CollectOrg'
        };
        if (this.favoritesList.findIndex(item => item[this.idField] === id) !== -1) {
            param.deleteList = [id];
            action = 'delete';
        } else {
            param.addList = [id];
        }
        this.curdRecentContactsOrFavorites('modify', param).subscribe(() => {
            if (action === 'add') {
                const index = this.favoritesList.findIndex(item => item[this.idField] === id);
                if (index === -1) {
                    this.favoritesList.push(object);
                }
            } else {
                this.favoritesList = this.favoritesList.filter(item => item[this.idField] !== id);
            }
            this.setFavoritesList(this.favoritesList);
        });
        e.cancelBubble = true;
        e.stopPropagation();
    }

    /**
     * 清空我的收藏
     */
    clearFavoritesList() {
        if (!this.favoritesList.length) {
            this.setFavoritesList(this.favoritesList);
            return;
        }
        const ids = this.favoritesList.map(item => item[this.idField]);
        const param: any = {
            absOrgType: this.absOrgType,
            bizId: this.busNum,
            objectValue: 'CollectOrg',
            addList: [],
            deleteList: ids
        };

        this.curdRecentContactsOrFavorites('modify', param).subscribe((val) => {
            this.favoritesList = [];
            this.setFavoritesList(this.favoritesList);
        });
    }
}
